<template>
  <div>
    <router-link to="login">
      <img
        @click="logout"
        class="close-account-page"
        src="@/assets/havr-icon/close-drawer-icon.svg"
      >
    </router-link>
    <div v-on:click.self="step1FormActive = 1" class="center-container justify-content-center">
      <div class="jumbo">
        <h2 class="heading-x-large">
          New password
        </h2>
        <br>
        <p class="paragraph-small-regular-secondary">Min 8 characters, including 1 uppercase letter,
          <br>1 number and 1 special character.
        </p>
        <br>
        <div>
          <div class="form-group">
            <label
              :class="{'active-input': step1FormActive ===  true}"
              class="paragraph-small-regular-secondary"
            >Create a new password</label>
            <input
              @click="step1FormActive = true"
              v-model="newPassword"
              type="password"
              class="form-control form-control-lg custom-size havr-input customIndex"
              required
            >
            <br>
            <label
              :class="{'active-input': step1FormActive === false}"
              class="paragraph-small-regular-secondary"
            >Confirm your new password</label>
            <input
              @click="step1FormActive = false"
              v-model="newPasswordConfirmation"
              type="password"
              class="form-control form-control-lg custom-size havr-input customIndex"
              required
            >
          </div>
          <div class="input-group">
            <div class="input-group-prepend">
              <button id="formButton" @click="saveNewPassword()" class="reset-password-button">Save</button>
            </div>
            <div class="input-group-prepend">
              <button
                id="formButton"
                @click="logout()"
                class="reset-password-button cancel-button"
              >Cancel</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import userRespository from "@/repository/users";
export default {
  data() {
    return {
      newPassword: null,
      step1FormActive: 1,
      newPasswordConfirmation: null,
      passwordRegex: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d.*)(?=.*\W.*)[a-zA-Z0-9\S]{8,50}$/m
    };
  },
  methods: {
    async saveNewPassword() {
      if (this.newPassword === this.newPasswordConfirmation) {
        const regex = RegExp(this.passwordRegex);
        if (!regex.test(this.newPassword)) return this.notifyBadPassword();
        const res = await userRespository.changeForgottedPassword(
          this.$route.query.password_reset_token,
          this.newPassword
        );
        if (res) this.successEditPasswordAndRedirect();
        else this.errorEditPassword();
      } else {
        this.$notify({
          message: "Passwords are not the same",
          type: "danger",
          timeout: 2000,
          horizontalAlign: "right",
          verticalAlign: "top"
        });
      }
    },
    redirectToLogin() {
      this.$router.push("login");
    },
    notifyBadPassword() {
      this.$notify({
        message: "Error : password format is incorrect",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    successEditPasswordAndRedirect() {
      this.$notify({
        message:
          "Your password has been changed, you will be redirected to the login page.",
        type: "success",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
      setTimeout(() => this.$router.push("/login"), 2000);
    },
    errorEditPassword() {
      this.$notify({
        message: "Error : internal error",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    logout() {
      window.sessionStorage.removeItem("currentUser");
      window.sessionStorage.removeItem("token");
      return this.$router.push("login");
    }
  }
};
</script>

