    <div id="sidebar-wrapper">
      <div v-if="store.state.keychain.loadDetails === true">
        <vue-lottie :data="loadingAnimation" loop autoplay ref="lottie"></vue-lottie>
      </div>
      <ul v-if="store.state.keychain.loadDetails === false" class="sidebar-nav">
