            <span v-if="keychainRepository.actvityKey.length > 0">
              <img
                v-if="keychainRepository.actvityKey[keychainRepository.actvityKey.length - 1].door_lock_status === 'locked'"
                class="detail-icon"
                src="@/assets/havr-icon/Lock.svg"
              >
              <i v-else class="fa fa-unlock-alt detail-icon" aria-hidden="true"></i>
              {{keychainRepository.actvityKey[keychainRepository.actvityKey.length - 1].door_lock_status}}
            </span>
            <span v-else>
              <i class="fa fa-unlock-alt detail-icon" aria-hidden="true"></i>
              Unlocked
            </span>
          </p>
        </li>

        <p
          class="sub-nav"
          v-if="store.state.keychain.accessManagement.users && store.state.keychain.accessManagement.users.length > 0 || store.state.keychain.accessManagement.admin.length > 0 "
          href="#"
        >Access management</p>
        <li
          v-if="store.state.keychain.accessManagement.admin && store.state.keychain.accessManagement.admin.length > 0"
        >
