                :style="{'background-image': 'url('+ 'data:image/png;base64,'+ i.picture +')'}"
              ></div>
              <img v-else class="avatar-activity" src="@/assets/img/user.png">
            </div>
            <p
              v-if="store.state.keychain.accessManagement.admin"
              class="text-sub-category avatar-number"
            >{{store.state.keychain.accessManagement.admin.length}} admin(s)</p>
          </div>
        </li>
        <li
          v-if="store.state.keychain.accessManagement.users && store.state.keychain.accessManagement.users.length > 0"
        >
          <div class="avatar-activity-container">
            <div v-for="i in store.state.keychain.accessManagement.users.slice(0, 5)">
              <div
                v-if="i.picture !== undefined"
                class="avatar-activity"
                :style="{'background-image': 'url('+ 'data:image/png;base64,'+ i.picture +')'}"
              ></div>
              <img v-else class="avatar-activity" src="@/assets/img/user.png">
            </div>
            <p
              class="text-sub-category avatar-number"
            >{{store.state.keychain.accessManagement.users.length}} user(s)</p>
          </div>
        </li>
        <li>
          <p class="sub-nav" href="#">Activity</p>
        </li>

        <div v-if="store.state.keychain.keylog.length > 0">
          <li
            v-for="i in orderLog.slice(0, defaultIndexNotification)"
            v-if="i.fullName !== undefined"
            class="item"
          >
            <p class="text-item">
              <img
                v-if="i.picture !== undefined"
                class="detail-item notification-profile"
                :src="'data:image/png;base64,'+ i.picture"
              >
              <img v-else class="detail-item notification-profile" src="@/assets/img/user.png">
              {{i.fullName}}
            </p>
            <p class="sub-log">
              <span v-if="i.door_lock_status === 'locked'">closed</span>
              <span v-else>opened</span>
              {{getDiffTime(i.created_at)}} ago
            </p>
          </li>
        </div>
        <center>
          <br>

          <li v-if="!store.state.keychain.keylog">
            <p class="text-item">No recent activity</p>
          </li>

          <div v-else>
            <div v-if="store.state.keychain.keylog.length > 0">
              <span
                v-if="defaultIndexNotification === 5"
                @click="defaultIndexNotification = orderLog.length"
                class="showAll keychain-notification load-more-container"
              >Load more</span>
              <span
                v-else
                @click="defaultIndexNotification = 5"
                class="showAll keychain-notification load-more-container"
              >Close</span>
            </div>
          </div>
        </center>
      </ul>
    </div>
  </div>
</template>

<script>
import { dataRepository } from "@/repository/login";
import store from "@/flulux/store";
import moment from "moment";
import lodash from "lodash";
import vueLottie from "vue-lottie-ssr";
import loadingAnimation from "@/assets/lottie/drawer_loading.json";
import repository from "@/repository/keychain";
import { keychainRepository } from "@/repository/keychain";
export default {
  data() {
    return {
      store: store,
      loadingAnimation,
      keychainRepository: keychainRepository,
      defaultIndexNotification: 5
    };
  },
  components: {
    vueLottie
  },
  computed: {
    lottie() {
      return this.$refs.lottie;
    },
    orderLog() {
      return this.store.state.keychain.keylog.sort(
        (a, b) => new Date(b.created_at) - new Date(a.created_at)
      );
    }
  },
  methods: {
    /**
     * get time notification
     */
    getDiffTime(p) {
      const now = moment.utc(p).local();
      const end = moment.utc().local();
      return moment.duration(now.diff(end)).humanize();
    }
  }
};
</script>

