<template>
  <div>
    <router-link to="/keychain">
      <img class="close-account-page" src="@/assets/havr-icon/close-drawer-icon.svg">
    </router-link>
    <div class="layout-body">
      <div class="jumbo">
        <p class="container-title heading-x-large">Edit your profile</p>
      </div>

      <input
        @change="updateProfilePicture"
        ref="pictureInput"
        v-show="showHover"
        type="file"
        id="avatar"
        name="avatar"
        accept="image/png, image/jpeg"
      >

      <div
        @mouseover="showEdit = true"
        v-if="!showEdit && user.profile_picture !== null && newProfilePicture !== ''"
        class="rounded profile"
        :style="{'background-image': 'url(data:image/png;base64,'+ user.profile_picture + ')'}"
      ></div>
      <img
        src="@/assets/img/user.png"
        @mouseover="showEdit = true"
        v-if="!showEdit && user.profile_picture === null && newProfilePicture === null"
        class="rounded profile"
      >

      <div
        @mouseleave="showEdit = false"
        v-if="showEdit && user.profile_picture !== null && newProfilePicture !== ''"
        class="rounded hover-profile"
        :style="{'background-image': 'url(data:image/png;base64,'+ user.profile_picture + ')'}"
      >
        <div class="profile-picture-container">
          <p
            @click="changePicture"
            class="profile-picture-actions profile-picture-edit-button"
          >Upload picture</p>
          <p
            @click="removePicture"
            class="profile-picture-actions profile-picture-remove-button"
          >Remove picture</p>
        </div>
      </div>

      <div
        @mouseleave="showEdit = false"
        v-if="showEdit && (user.profile_picture === null && newProfilePicture === null)"
        class="rounded hover-profile"
        :style="{'background-image': 'url('+ imageUrl +')'}"
      >
        <div class="profile-picture-container">
          <p
            @click="changePicture"
            class="profile-picture-actions profile-picture-edit-button"
          >Upload picture</p>
          <p
            @click="removePicture"
            class="profile-picture-actions profile-picture-remove-button"
          >Remove picture</p>
        </div>
      </div>

      <div class="section-form">
        <div>
          <div class="form-group">
            <form class="form-group">
              <div class="row">
                <div class="col">
                  <label>First name</label>
                  <input
                    v-model="user.first_name"
                    type="text"
                    class="havr-black-input form-control"
                  >
                </div>
                <div class="col">
                  <label>Last name</label>
                  <input v-model="user.last_name" type="text" class="havr-black-input form-control">
                </div>
              </div>
            </form>
            <div class="form-group">
              <label>Username</label>
              <input v-model="user.username" class="form-control havr-black-input">
            </div>
            <div class="form-group">
              <label>Email adress</label>
              <input v-model="user.email" class="form-control havr-black-input">
            </div>
          </div>
          <button
            @click="updateProfil"
            id="formButton"
            :disabled="hasChange"
            class="btn btn-primary havr-black-color"
          >Save</button>
        </div>

        <p @click="showPasswordEditModal = true, password = null" class="change-password">
          Change password
          <i class="fa fa-chevron-right align-center-icn" aria-hidden="true"></i>
        </p>
      </div>
    </div>
    <b-modal
      :hide-footer="true"
      :ok-title="'Save'"
      :hide-header="true"
      :ok-variant="'havr-black-color'"
      centered
      v-model="showPasswordEditModal"
    >
      <div class="form-group">
        <label>Type in your current password</label>
        <input v-model="password" type="password" class="form-control havr-black-input">
      </div>
      <div class="form-group">
        <label for="exampleInputEmail1">Type in your new password</label>
        <input v-model="newPassword" type="password" class="form-control havr-black-input">
        <small
          class="form-text text-muted"
        >Min 8 characters, 1 Lowercase letter, 1 Uppercase letter, 1 number and 1 special character</small>
      </div>
      <div class="form-group">
        <label>Confirm your new password</label>
        <input v-model="confirmNewPassword" type="password" class="form-control havr-black-input">
      </div>
      <button @click="updatePassword" class="btn btn-primary havr-black-color modal-button">Save</button>
      <button
        @click="cancelPasswordValidation"
        class="btn btn-primary havr-black-inversed modal-button"
      >Cancel</button>
    </b-modal>

    <b-modal
      :hide-footer="true"
      :ok-title="'Save'"
      :hide-header="true"
      :ok-variant="'havr-black-color'"
      centered
      v-model="showPasswordModal"
    >
      <div class="form-group">
        <label>Please enter your password to save your modifications</label>
        <input v-model="password" type="password" class="form-control havr-black-input">
      </div>
      <button @click="updateEmail" class="btn btn-primary havr-black-color modal-button">Save</button>
      <button
        @click="showPasswordModal = !showPasswordModal"
        class="btn btn-primary havr-black-inversed modal-button"
      >Cancel</button>
    </b-modal>
  </div>
</template>

<script>
import bModal from "bootstrap-vue/es/components/modal/modal";
import bModalDirective from "bootstrap-vue/es/directives/modal/modal";
import repository from "@/repository/users";
import PictureInput from "vue-picture-input";
import notifiy from "vue-notification";
export default {
  components: {
    "b-modal": bModal,
    PictureInput
  },
  directives: {
    "b-modal": bModalDirective
  },
  data() {
    return {
      showHover: false,
      user: JSON.parse(sessionStorage.getItem("currentUser")),
      originalEmail: JSON.parse(sessionStorage.getItem("currentUser")).email,
      originalProfil: JSON.parse(sessionStorage.getItem("currentUser")),
      usernameRegex: /^[a-zA-Z0-9]+$/,
      passwordRegex: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d.*)(?=.*\W.*)[a-zA-Z0-9\S]{8,50}$/m,
      showPasswordModal: false,
      showPasswordEditModal: false,
      showUpdatePicture: null,
      showEdit: false,
      newPassword: null,
      newProfilePicture: null,
      confirmNewPassword: null,
      password: null,
      emailRegex: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    };
  },
  computed: {
    imageUrl() {
      return require("@/assets/img/user.png");
    },
    hasChange() {
      if (
        this.user.username !== this.originalProfil.username ||
        this.user.last_name !== this.originalProfil.last_name ||
        this.user.first_name !== this.originalProfil.first_name ||
        this.user.email !== this.originalEmail ||
        this.user.profile_picture !== this.originalProfil.profile_picture
      )
        return false;
      return true;
    }
  },
  methods: {
    async updateProfil() {
      if (this.newProfilePicture !== null) {
        const resPicture = await repository.updateProfilePicture({
          extension: "jpeg",
          base64_picture: this.newProfilePicture.substring(
            this.newProfilePicture.indexOf(",") + 1
          )
        });
        if (!resPicture)
          return this.notifyError("Error : profile picture is too big");
        this.user.profile_picture = this.newProfilePicture.substring(
          this.newProfilePicture.indexOf(",") + 1
        );
        sessionStorage.setItem("currentUser", JSON.stringify(this.user));
      }
      const userUpdate = {
        username: this.user.username.trim(),
        last_name: this.user.last_name.trim(),
        first_name: this.user.first_name.trim()
      };
      if (!this.checkInputs(userUpdate))
        return this.notifyError("Error : All fields are mandatory");
      if (this.checkEmail(this.user.email) === false)
        return this.notifyError("Error : Bad email format");

      if (this.originalProfil.email !== this.user.email) {
        const checkMail = await repository.checkUsernameOrEmail(
          userUpdate.email
        );
        if (checkMail.already_exists) {
          return this.notifyError("Error: Email is already used");
        }
      }

      if (this.user.username !== this.originalProfil.username) {
        const checkUsername = await repository.checkUsernameOrEmail(
          userUpdate.username
        );
        if (checkUsername.already_exists) {
          return this.notifyError("Error: Username is already used");
        }
      }

      if (this.originalEmail !== this.user.email) {
        return this.showPasswordValidationModal();
      } else this.saveProfil(userUpdate);
    },

    changePicture() {
      this.$refs.pictureInput.click();
    },
    async saveProfil(userUpdate) {
      if (this.checkUsername(userUpdate.username) === false)
        return this.notifyError("Error : Bad username format");
      const res = await repository.updateProfil(userUpdate, this.user.id);
      if (!res) this.notifyError("Error : Internal Error");
      else {
        this.notifySuccess("Profil updated with success !", 2000);
        sessionStorage.setItem("currentUser", JSON.stringify(this.user));
        this.user = JSON.parse(sessionStorage.getItem("currentUser"));

        this.originalProfil = JSON.parse(sessionStorage.getItem("currentUser"));
        this.originalEmail = JSON.parse(
          sessionStorage.getItem("currentUser")
        ).email;
      }
    },
    showPasswordValidationModal() {
      this.showPasswordModal = true;
    },
    async updateEmail() {
      const emailUpdate = { email: this.user.email, password: this.password };
      const res = await repository.updateEmail(emailUpdate);
      if (res) {
        this.notifySuccess(
          "We sent you an email to " +
            this.user.email +
            ". Please click on the link inside the email to confirm your new email adress",
          5000
        );
        this.showPasswordModal = false;
        this.password = null;
      } else this.notifyError("Error : incorrect password");
      this.saveProfil();
    },
    updateProfilePicture(e) {
      const files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
    },
    createImage(file) {
      const image = new Image();
      const reader = new FileReader();
      reader.onload = e => {
        this.newProfilePicture = e.target.result.substring(
          e.target.result.indexOf(",") + 1
        );
        this.user.profile_picture = e.target.result.substring(
          e.target.result.indexOf(",") + 1
        );
      };
      reader.readAsDataURL(file);
    },
    removePicture() {
      this.user.profile_picture = null;
      this.newProfilePicture = null;
    },
    async updatePassword() {
      if (this.checkPasswordValidation(this.newPassword) === false) {
        return this.notifyError(
          "Error: Password must be minimum 8 characters, 1 Lowercase letter, 1 Uppercase letter, 1 number and 1 special character."
        );
      }
      if (this.newPassword !== this.confirmNewPassword)
        return this.notifyError("Error: Password confirmation dont match.");
      const password = {
        new_password: this.newPassword,
        current_password: this.password
      };
      const res = await repository.updatePassword(password);
      if (res) {
        this.showPasswordEditModal = false;
        this.refreshPasswordEditForm();
        this.notifySuccess("Password updated with success", 2000);
      } else this.notifyError("Error: incorrect password");
    },
    cancelPasswordValidation() {
      this.showPasswordEditModal = !this.showPasswordEditModal;
      this.refreshPasswordEditForm();
    },
    refreshPasswordEditForm() {
      this.password = null;
      this.newPassword = null;
      this.confirmNewPassword = null;
    },
    checkInputs(p) {
      for (const i in p) {
        if (p[i].trim() === "") return false;
      }
      return true;
    },
    notifySuccess(msg, time) {
      return this.$notify({
        message: msg,
        type: "success",
        timeout: time,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    checkEmail(p) {
      return RegExp(this.emailRegex).test(p);
    },
    checkUsername(p) {
      return RegExp(this.usernameRegex).test(p);
    },
    checkPasswordValidation(p) {
      return RegExp(this.passwordRegex).test(p);
    },
    notifyError(p) {
      return this.$notify({
        message: p,
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    }
  }
};
</script>

