<template>
  <div>
    <user-details-drawer v-if="store.state.user.detailsUser !== undefined"></user-details-drawer>
    <card class="card-plain">
      <div class="table-full-width table-responsive">
        <user-table
          type="hover"
          :data="table.data"
          :columns="table.columns"
          :maxRow="table.data.length"
        ></user-table>
      </div>
    </card>
  </div>
</template>
<script>
import UserTable from "@/components/UserTable";
import moment from "moment";
import repository from "@/repository/users";
import { userDataRepository } from "@/repository/users";
import UserDetailsDrawer from "@/layout/dashboard/UserDetailsDrawer";
import store from "@/flulux/store";
export default {
  components: {
    UserTable,
    UserDetailsDrawer
  },
  data() {
    return {
      table: {
        title: "",
        columns: ["name", "username", "last activity"],
        data: userDataRepository.usersTable
      },
      store: store
    };
  },
  mounted() {
    if (!window.sessionStorage.getItem("token")) {
      return this.$router.push("login");
    }
    if (userDataRepository.usersTable.length === 0) {
      this.$router.push("loading-data");
    }
    this.setMaxPagination(userDataRepository.contacts.length);
  },
  methods: {
    setMaxPagination(p) {
      if (p <= 20) {
        this.store.actions.SET_END_PAGINATION_USERS(p);
      } else this.store.actions.SET_END_PAGINATION_USERS(20);
    }
  }
};
</script>
<style>
.card-body {
  overflow: -webkit-paged-x;
}
</style>
