<template>
  <div id="wrapper-map">
    <div
      id="sidebar-wrapper"
      class="sidebar-map"
      :class="{'overlay-map': store.state.map.floor_map.tutorialDrawer}"
    >
      <li class="sidebar-brand">
        <img
          @click="removeSelection"
          class="go-back-arrow"
          src="@/assets/havr-icon/left-arrow.svg"
          v-if="selectedGroup.id"
        >
        <h3 v-if="!selectedGroup.id" class="drawer-map-title">Keychain</h3>
        <h3 v-else class="drawer-map-title">{{selectedGroup.description}}</h3>
        <p
          v-if="!selectedGroup.id"
          class="drawer-map-subtitle"
        >Start by choosing a group to display its keys</p>
      </li>
      <div class="container group-container">
        <div class="row group-list">
          <groups-card-list v-if="!selectedGroup.id"/>
          <KeyCardList :group="selectedGroup" v-else/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GroupsCardList from "@/components/GroupsCardsList";
import KeyCardList from "@/components/KeyCardList";
import store from "@/flulux/store";
import FloorMapRepository from "@/repository/map";
export default {
  components: {
    GroupsCardList,
    KeyCardList
  },
  data() {
    return {
      store: store,
      selectedGroup: {
        id: null
      },
      usedKeys: []
    };
  },
  mounted() {
    this.$root.$on(
      "SET_GROUP",
      selectedGroup => (this.selectedGroup = selectedGroup)
    );
    this.getAllUsedKeys();
  },
  methods: {
    removeSelection() {
      this.selectedGroup = { id: null };
    },
    async getAllUsedKeys() {
      const data = await FloorMapRepository.getAllFloorMaps();
      data.map(item =>
        JSON.parse(item.positions).forEach(pos => {
          this.usedKeys.push(pos.door_lock_id);
        })
      );
      this.store.state.map.usedKeys = this.usedKeys;
    }
  },
  computed: {}
};
</script>

