<template>
<div>
   <input id="fileInput" @change="updateMapPicture" ref="uploadMap" type="file"  accept=".png,.jpg,.jpeg">
    <div  v-show="store.state.map.currentFloor.picture === null">
      <center class="centered">
        
           
            <p class='title-no-maps '>You don't have any map</p>
            <button  @click="uploadMap" class="btn btn-primary havr-black-color map-button" >Add a map</button>
            <p class="title-advice"> Supported format : JPG, JPEG, PNG</p>
            <p class="title-advice">Max size : 2 MB</p>
      </center>
    </div>
       

         


    <div class="maps-container" v-if="store.state.map.currentFloor.picture">
        <div v-if="store.state.map.currentFloor.picture && tutoStep1 && store.state.map.floor_map.tutorial" class="step2Tuto">
          <img src="@/assets/havr-icon/leftTutorialArrow.svg" class="tutorial1-arrow">
            <p class="title-no-maps ">Give a name to this map here</p>
            <button  @click="tutoStep1 = false, store.state.map.floor_map.tutorialDrawer = false, tutoStep2 = true" class="btn btn-primary havr-black-color map-button  tutoStep1Button" >OK</button>
           
        </div>


 <div v-if="store.state.map.currentFloor.picture && tutoStep3 && !store.state.map.currentDraggedKey && store.state.map.floor_map.tutorial && selectedGroup" class="step3Tuto">
           <img src="@/assets/havr-icon/downTutorialArrow.svg" class="step2tuto-arrow">
            <p class="title-no-maps ">Drag and drop keys on the map</p>
            <button  v-if="tutoStep3" @click="store.state.map.floor_map.tutorial = false, tutoStep3 = false" class="btn btn-primary havr-black-color map-button tutoStep3Button" >OK</button>
        </div>


          <div v-if="store.state.map.currentFloor.picture && tutoStep2 && !selectedGroup && store.state.map.floor_map.tutorial" class="step1Tuto">
           <img src="@/assets/havr-icon/downTutorialArrow.svg" class="step2tuto-arrow">
            <p class="title-no-maps ">Start by clicking on a group to see its keys</p>
            <button  v-if="tutoStep2" @click="tutoStep2 = false, tutoStep3 = true" class="btn btn-primary havr-black-color map-button tutoStep2Button" >OK</button>
        </div>
        <floor-map-edit :class="{'overlay-map': store.state.map.floor_map.tutorial }"  :src="'data:image/png;base64,' + store.state.map.currentFloor.picture" />
    </div>

</div>
</template>

<script>
import store from "@/flulux/store";
import fabric from "fabric";
import $ from "jquery";
import mapRepository from "@/repository/map";
import FloorMapEdit from "./FloorMapEdit";
import notify from "vue-notification";
import { setTimeout } from "timers";
export default {
  components: {
    FloorMapEdit
  },
  data() {
    return {
      store: store,
      tutoStep1: true,
      tutoStep2: false,
      tutoStep3: false,
      selectedGroup: false
    };
  },
  async mounted() {
    if (this.store.state.map.currentFloor.picture) {
      this.tutoStep1 = false;
      this.store.state.map.floor_map.tutorial = false;
      this.store.state.map.floor_map.tutorialDrawer = false
    }
    this.$root.$on("SAVE_MAP", () => this.saveMap());
    this.$root.$on(
      "SET_GROUP",
      selectedGroup => ((this.selectedGroup = true), (this.tutoStep3 = true))
    );
  },
  methods: {
    uploadMap() {
      this.$refs.uploadMap.click();
    },
    updateMapPicture(e) {
      const files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
    },
    errorSizeMap() {
      this.$notify({
        message: "Map size is too large (max 2mb)",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    async createImage(file) {
      const image = new Image();
      const reader = new FileReader();
      reader.onload = e => {
        if (e.total > 200000) return this.errorSizeMap();
        this.store.state.map.currentFloor.picture = e.target.result.substring(
          e.target.result.indexOf(",") + 1
        );
        this.store.state.map.floor_map.tutorial = true;
        this.store.state.map.floor_map.tutorialDrawer = true;
        this.store.actions.OPEN_GROUP_MAP_DRAWER(true);
        this.$root.$emit("show_top_name_navbar", true);
        this.$notify({
          message: "Map imported with success",
          type: "success",
          timeout: 2000,
          horizontalAlign: "center",
          verticalAlign: "top"
        });
      };
      reader.readAsDataURL(file);
    }
  }
};
</script>

