import { ENDPOINTS, API_BASE_URL } from "../constant.js";
import { dataRepository } from "@/repository/login";
import loginRepository from "./login";
import lodash from "lodash";
export default {
  /**
   * login wrapper to get access token
   * @param {access_token} p
   */
  async getDoorLocks(p) {
    const res = await loginRepository.retrieveToken();
    const lockDoor = await this.retrieveLockDoor(res);
  },

  async getSharedK(p) {
    const res = await loginRepository.retrieveToken();
    const sharedKey = await this.retrieveSharedKeys(res);
  },

  async getLockGroupFromId(id) {
    if (id) {
      const resp = await fetch(API_BASE_URL + ENDPOINTS.lock_group_info + id, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer: " + sessionStorage.getItem("token")
        }
      });
      const res = await resp.json();
      return res;
    }
  },

  async getSpecificLockDoor(id) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.specific_door_lock + id, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      }
    });
    const res = await resp.json();
    return res;
  },

  async getNbUsersSharedKey(p, admin) {
    if (admin) {
      const users = await this.getAccessManagement(p);
      return "shared to " + users.length + " persons";
    } else return "";
  },

  async retrieveSharedKeys(token) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.shared_keys, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      }
    });
    if (resp.status === 200) {
      let parsedResp = await resp.json();
      const res = await Promise.all(
        parsedResp.map(async i => ({
          battery_level: i.door_lock.battery_level,
          city: i.door_lock.city,
          description: i.door_lock.description,
          sharing: await this.getNbUsersSharedKey(i.door_lock.id, i.admin),
          ...i
        }))
      );
      return (keychainRepository.shared_key = [...res]);
    }
  },

  async shareKey(p) {
    const res = await fetch(API_BASE_URL + ENDPOINTS.share_key, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      },
      body: JSON.stringify({ shared_key: p })
    });

    return res.status;
  },

  async getLockGroupsWithDoorlocks(token) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.lock_groups, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      }
    });
    if (resp.status === 200) {
      return await resp.json();
    }
  },

  async getAccessManagement(p) {
    const res = await loginRepository.retrieveToken();
    const sharedKey = await this.retrieveAccessManagement(res, p);
    return sharedKey;
  },

  async retrieveAccessManagement(token, p) {
    const res = await fetch(API_BASE_URL + ENDPOINTS.accessManagement, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      },
      body: JSON.stringify({ shared_key: { door_lock_id: parseInt(p) } })
    });
    let logs = await res.json();
    return logs;
  },

  /**
   * get all doorlock of a user
   * @param {user token} token
   */
  async retrieveLockDoor(token) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.keychain, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      }
    });
    if (resp.status === 200) {
      const res = await resp.json();
      const key = await Promise.all(
        res.map(async i => ({
          sharing: await this.getNbUsersSharedKey(i.id, true),
          ...i
        }))
      );
      return (keychainRepository.door_locks = [...key]);
    } else {
      return (location.href = "/#/login");
    }
  },

  async getLogs(p) {
    const res = await loginRepository.retrieveToken();
    const logs = await this.retrieveLockDoorLogs(p, res);
  },

  /**
   * get log of key
   */
  async retrieveLockDoorLogs(p, token) {
    const res = await fetch(API_BASE_URL + ENDPOINTS.doorlocklog, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      },
      body: JSON.stringify({ door_lock: { door_lock_id: parseInt(p) } })
    });
    const logs = await res.json();
    return (keychainRepository.actvityKey = [...logs]);
  }
};

export let keychainRepository = {
  door_locks: [],
  shared_key: [],
  actvityKey: [],
  accessManagement: {}
};
