<template>
  <nav class="navbar navbar-expand-lg navbar-light">
    <div class="container-fluid fixed-header">
      <input
        id="fileInput"
        @change="updateMapPicture"
        ref="uploadMap"
        type="file"
        accept=".png, .jpg, .jpeg"
        style="display: none"
      >
      <ul class="header-content map-header">
        <li>
          <a class="navbar-brand heading-x-large" href="#">Keychain</a>
        </li>

        <li class="filter-container">
          <button @click="uploadMap" class="action-map-button">
            <img class="map-actions" src="@/assets/havr-icon/plus.svg">
            <span class="align-middle">New map</span>
          </button>
        </li>
        <li class="keychain-selector-map">
          <div v-if="routeName === 'Keychain' || 'Keychain-map-list'" class="selector-item">
            <router-link
              :class="{'active-view-selector' : routeName === 'Keychain'}"
              class="selector-menu"
              to="Keychain"
            >
              <p
                :class="{'active-view-text-selector' : routeName === 'Keychain'}"
                class="item-text"
              >
                <img class="selector-img" src="@/assets/havr-icon/list.svg"> list
              </p>
            </router-link>
            <router-link
              :class="{'active-view-selector' : routeName === 'Keychain-map-list'}"
              to="keychain-map-list"
              class="selector-menu"
            >
              <p
                :class="{'active-view-text-selector' : routeName === 'Keychain-map-list'}"
                class="item-text"
              >
                <img class="selector-img" src="@/assets/havr-icon/map-marker.svg"> Map
              </p>
            </router-link>
          </div>
        </li>
      </ul>
      <div class="collapse navbar-collapse">
        <ul class="navbar-nav ml-auto"></ul>
      </div>
    </div>
  </nav>
</template>

<script>
import store from "@/flulux/store";
export default {
  data() {
    return {
      store: store
    };
  },
  computed: {
    routeName() {
      const { name } = this.$route;
      return name;
    }
  },
  methods: {
    newMap() {
      this.store.state.map.currentFloor = {
        name: null,
        picture: null,
        markers: [],
        file_extension: "png"
      };
    },
    uploadMap() {
      this.newMap();
      this.$refs.uploadMap.click();
    },
    updateMapPicture(e) {
      const files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
    },
    errorSizeMap() {
      this.$notify({
        message: "Map size is too large (max 2mb)",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    async createImage(file) {
      const image = new Image();
      const reader = new FileReader();
      reader.onload = e => {
        if (e.total > 200000) return this.errorSizeMap();
        this.store.state.map.currentFloor.picture = e.target.result.substring(
          e.target.result.indexOf(",") + 1
        );
        this.store.state.map.floor_map.tutorial = true;
        this.store.state.map.floor_map.tutorialDrawer = true;
        this.store.actions.OPEN_GROUP_MAP_DRAWER(true);
        this.$root.$emit("show_top_name_navbar", true);
        this.$notify({
          message: "Map imported with success",
          type: "success",
          timeout: 2000,
          horizontalAlign: "center",
          verticalAlign: "top"
        });
      };
      reader.readAsDataURL(file);
      this.$router.push("Keychain-map");
    }
  }
};
</script>

<style scoped>
.filter-map {
  width: 97px;
}
.map-actions {
  width: 12px;
}

.keychain-selector-map {
  position: relative;
  top: 2em;
}
</style>
