<template>
  <div>
    <span class="sub-title-step">
      <span id="usersSelected">{{selectedUsers}}</span> users selected. You can also
      <span
        @click="setOrUnset(true)"
        class="sub-title-actions"
      >select all</span> or
      <span @click="setOrUnset(false)" class="sub-title-actions">clear all</span>
    </span>
    <div class="users">
      <div
        @click="selectUser(user, i)"
        :key="user.id"
        class="user-container"
        v-for="(user, i) in users"
      >
        <img
          v-if="user.picture"
          class="avatar-activity"
          :src="'data:image/png;base64,'+ user.picture"
        >
        <img v-else class="avatar-activity" src="@/assets/img/user.png">
        <span class="name-container">
          {{user.user.first_name}} {{user.user.last_name}}
          <img
            v-if="user.selected"
            class="selected-img"
            src="@/assets/img/outline.png"
          >
        </span>
      </div>
    </div>
  </div>
</template>
<script>
import store from "@/flulux/store";
import userRepository from "@/repository/users";
import lodash from "lodash";
export default {
  data() {
    return {
      users: [],
      store: store,
      selectedUsers: 0
    };
  },
  async mounted() {
    const res = await this.getUsers();
    if (this.store.state.shareKey.user_list_ids.length > 0) {
      this.mapSelectedFromStore();
    }
  },
  computed: {},
  methods: {
    selectedUser() {
      const val = lodash.filter(this.users, { selected: true });
      this.selectedUsers = val.length;
    },
    async getUsers() {
      const users = await userRepository.retrieveContacts();
      const picture_profile = await userRepository.retrieveContactPictures(
        this.mapUsersId(users)
      );
      this.mapPictureProfile(picture_profile, users);
    },
    mapUsersId(p) {
      return p.map(i => i.user.id);
    },
    mapPictureProfile(picture_profile, users) {
      users.map(user => {
        user.selected = false;
        picture_profile.map(picture => {
          if (user.user.id === picture.user_id) {
            user.picture = picture.base64;
          }
        });
        this.users.push(user);
      });
    },
    mapSelectedFromStore() {
      this.store.state.shareKey.user_list_ids.forEach(id => {
        this.users.forEach((user, i) => {
          if (user.id === id) {
            user.selected = true;
            this.$set(this.users, i, user);
          }
        });
      });
    },
    setOrUnset(p) {
      if (p) this.notifySelection("All contacts selected");
      if (!p) this.notifySelection("All contacts unselected");
      this.users.forEach((i, index) => {
        i.selected = p;
        this.$set(this.users, index, i);
      });
      this.setUsersId();
      this.selectedUser();
    },

    notifySelection(p) {
      this.$notify({
        message: p,
        type: "info",
        horizontalAlign: "right",
        timeout: 2000,
        verticalAlign: "top"
      });
    },
    selectUser(p, i) {
      p.selected = !p.selected;
      this.$set(this.users, i, p);
      this.setUsersId();
      this.selectedUser();
    },
    setUsersId() {
      const filter = lodash.filter(this.users, { selected: true });
      const userIds = filter.map(user => user.user.id);
      this.store.actions.SET_SHARED_KEY_USERS(userIds);
    }
  }
};
</script>

