<template>
  <div style="    position: fixed;
    width: 100%; height:100%" class="row">
    <div v-on:click.self="state.stepFormActive = 0" class="col-6">
      <div class="login100-form validate-form">
        <span class="login100-form-title p-b-43">
          <img src="@/assets/img/havr-logo-full.svg">
        </span>
        <div class="form-group">
          <label
            :class="{'active-input': state.stepFormActive === 1}"
            @keyup.enter="login"
            class="label-input-login paragraph-small-regular-secondary"
          >Email or username</label>
          <input
            @click="state.stepFormActive = 1"
            placeholder="Email or username"
            v-model="state.email_or_username"
            class="form-control form-control-lg custom-size havr-input customIndex"
            type="text"
            name="text"
          >
        </div>
        <br>
        <label
          :class="{'active-input': state.stepFormActive === 2}"
          class="label-input-login paragraph-small-regular-secondary"
        >Password</label>
        <input
          @keyup.enter="login"
          placeholder="Password"
          v-model="state.password"
          @click="state.stepFormActive = 2"
          class="form-control form-control-lg custom-size havr-input customIndex"
          type="password"
          name="pass"
        >
        <div class="flex-sb-m w-full p-t-3 p-b-32 forgot-password">
          <div class="contact100-form-checkbox"></div>

          <div>
            <router-link to="/forgot-password">
              <p class="forgot-password-label paragraph-small-regular-secondary">Forgot Password ?</p>
            </router-link>
          </div>
        </div>

        <br>
        <div class="container-login100-form-btn">
          <button
            :disabled="state.password.length < 1 || state.email_or_username.length < 1"
            @click="login"
            class="login100-form-btn"
          >Sign in</button>
        </div>
      </div>
      <router-link to="/create-account" class>
        <p class="new-account paragraph-small-regular">
          First time here ?
          <b>Create an account</b>
        </p>
      </router-link>
    </div>

    <div
      v-on:click.self="state.stepFormActive = 0"
      :style="{'background-image': 'url('+ backgroundUrl +')', 'background-size': 'cover'}"
      class="col-6 cover-img"
    ></div>
  </div>
</template>

<script>
import loginRepository from "@/repository/login";
import { dataRepository } from "@/repository/login";
export default {
  data() {
    return {
      state: {
        grant_type: "password",
        email_or_username: "",
        password: "",
        stepFormActive: 0,
        client_id: "web_plateform_client_id",
        user_agent: navigator.userAgent
      }
    };
  },
  mounted() {
    if (sessionStorage.getItem("token")) return this.$router.go(-1);
  },
  computed: {
    backgroundUrl() {
      return require("@/assets/img/signin.png");
    }
  },
  methods: {
    /**
     * log user into application using data repository
     */
    login() {
      loginRepository
        .login(this.state)
        .then(res => this.checkIfUserIsConnected());
    },
    /**  * check acccess token props to log user
     */
    checkIfUserIsConnected() {
      if (!dataRepository.account.access_token) return this.showErrMsg();
      else this.$router.push("/loading-data");
    },
    /**     * bind error msg if credentials are wrong
     */
    showErrMsg() {
      this.$notify({
        message: "Incorrect password or username",
        type: "danger",
        horizontalAlign: "right",
        timeout: 2000,
        verticalAlign: "top"
      });
    }
  }
};
</script>



<style lang="scss" scoped>
@import "../assets/sass/login.scss";
</style>
