<template>
  <div class="moving-arrow" :style="arrowStyle">
  </div>
</template>
<script>
export default {
  props: {
    moveY: {
      type: Number,
      default: 0
    }
  },
  computed: {
    /**
     * Styles to animate the arrow
     * @returns {{transform: string}}
     */
    arrowStyle() {
      return {
        transform: `translate3d(0px, ${this.moveY}px, 0px)`
      };
    }
  }
};
</script>
<style lang="scss">
$bg-nude: #f4f3ef !default;

.moving-arrow {
     width: 87%;
    height: 54px;
    border-radius: 2px;
    background: #dcdcdc;
    display: inline-block;
    margin-left: 15px;
    position: absolute;
}
@media (max-width: 992px) {
  .moving-arrow {
    display: none;
  }
}
</style>
