import loginRepository from "./login";
import { dataRepository } from "./login";
import axios from "axios";
import lodash from "lodash";
import { ENDPOINTS, API_BASE_URL } from "../constant.js";
export default {
  /**
   * get user
   */
  async retrieveUser(p, token) {
    const usersCache = lodash.filter(userDataRepository.usersKey, { id: p });
    if (usersCache.length > 0) {
      return usersCache[0];
    }
    const resp = await fetch(API_BASE_URL + ENDPOINTS.user + p, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      }
    });
    if (resp.status === 403) {
      return false;
    } else {
      const user = await resp.json();

      if (usersCache.length === 0) {
        userDataRepository.usersKey.push(user);
      }
      return user;
    }
  },

  /**
   * Check occurency of username or email before account creation process
   * @param {username or email} p
   */
  async checkUsernameOrEmail(p) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.check_username_or_email, {
      method: "post",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ username_or_email: p })
    });

    if (resp.status === 403 || resp.status === 204) {
      return false;
    } else {
      const res = await resp.json();
      return res;
    }
  },

  async updateProfil(p, id) {
    const res = await axios({
      method: "patch",
      url: API_BASE_URL + ENDPOINTS.user + id,
      data: {
        user: p
      },
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      }
    });

    if (res.status !== 200) {
      return false;
    } else {
      return true;
    }
  },

  async updateEmail(p) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.change_user_email, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      },
      body: JSON.stringify({ user: p })
    });

    if (resp.status !== 200) {
      return false;
    } else {
      return true;
    }
  },

  /**
   * create user account
   */
  async createUserAccount(p) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.create_user, {
      method: "post",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(p)
    });

    if (resp.status === 403 || resp.status === 204) {
      return false;
    } else {
      const res = await resp.json();
      return res;
    }
  },

  /**
   * get profile contact picture
   */
  async retrieveContactPicture(p) {
    if (
      lodash.filter(userDataRepository.contactPictures, { id: p }).length > 0
    ) {
      return lodash.filter(userDataRepository.contactPictures, { id: p })[0]
        .img;
    }
    const resp = await fetch(API_BASE_URL + ENDPOINTS.contact_picture, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      },
      body: JSON.stringify({ user: { id: parseInt(p) } })
    });

    if (resp.status === 403 || resp.status === 204) {
      return false;
    } else {
      const res = await resp.json();
      if (
        lodash.filter(userDataRepository.contactPictures, { id: p }).length ===
        0
      ) {
        await userDataRepository.contactPictures.push({
          img: res.base64,
          id: p
        });
      }

      return res;
    }
  },

  /**
   * get profile contact pictures
   */
  async retrieveContactPictures(p, token) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.get_contact_pictures, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      },
      body: JSON.stringify({ user: { ids: p } })
    });

    if (resp.status === 403 || resp.status === 204) {
      return false;
    } else {
      const res = await resp.json();
      return res;
    }
  },

  /**
   * get contact shared key
   */
  async retrieveSharedKeyForUser(p, token) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.user_shared_keys, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      },
      body: JSON.stringify({ shared_key: { user_id: parseInt(p) } })
    });

    if (resp.status === 403 || resp.status === 204) {
      return false;
    } else {
      const res = await resp.json();
      return res;
    }
  },

  /**
   * get contact shared key
   */
  async retrieveLockDoorUserDetail(p, token) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.user_lock_logs, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      },
      body: JSON.stringify({ user: { user_id: parseInt(p) } })
    });

    if (resp.status === 403 || resp.status === 204) {
      return false;
    } else {
      const res = await resp.json();
      return res;
    }
  },

  async forgotPassword(p) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.forgot_password, {
      method: "post",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ forgot_password: { email: p } })
    });
    if (resp.status === 404) return false;
    if (resp.status === 204) return true;
  },

  async updatePassword(p) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.change_password, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      },
      body: JSON.stringify({ change_password: p })
    });

    if (resp.status !== 200) {
      return false;
    } else {
      return true;
    }
  },

  async changeForgottedPassword(token, p) {
    const data = {
      user: {
        password_reset_token: token,
        new_password: p
      }
    };
    const resp = await fetch(
      API_BASE_URL + ENDPOINTS.change_password_forgotten,
      {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data)
      }
    );
    if (resp.status !== 204) {
      return false;
    } else {
      return true;
    }
  },

  async updateProfilePicture(p) {
    try {
      const resp = await fetch(
        API_BASE_URL + ENDPOINTS.change_profile_picture,
        {
          method: "post",
          headers: {
            "Content-Type": "application/json",
            Authorization: "Bearer: " + sessionStorage.getItem("token")
          },
          body: JSON.stringify({ user: p })
        }
      );

      if (resp.status !== 200) {
        return false;
      } else {
        return true;
      }
    } catch (err) {
      return false;
    }
  },

  async retrieveContacts(token) {
    const resp = await fetch(API_BASE_URL + ENDPOINTS.contacts, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      }
    });
    if (resp.status === 200) {
      let parsedResp = await resp.json();
      return (userDataRepository.contacts = [...parsedResp]);
    }
  }
};

export let userDataRepository = {
  contactPictures: [],
  contacts: [],
  usersKey: [],
  usersTable: []
};
