<template>
  <div class="wrapper">
    <side-bar>
      
      <template slot="links">
         <sidebar-link v-on:click.native="emptyMapStepper" v-if="getCurrentRoute === 'Overview'" to="/overview"><img class="custom-icon"  src="@/assets/havr-icon/overview-active.svg"> <p class="custom-label active-link">Overview</p></sidebar-link>
          <sidebar-link v-on:click.native="emptyMapStepper"  v-if="getCurrentRoute !== 'Overview'" to="/overview"><img class="custom-icon"  src="@/assets/havr-icon/overview.svg"> <p class="custom-label">Overview</p></sidebar-link>
          <sidebar-link v-on:click.native="emptyMapStepper" v-if="getCurrentRoute === 'Keychain'" to="/keychain"><img class="custom-icon" src="@/assets/havr-icon/keychain-active.svg"> <p class="custom-label active-link">Keychain</p></sidebar-link>
           <sidebar-link v-on:click.native="emptyMapStepper" v-if="getCurrentRoute !== 'Keychain'" to="/keychain"><img class="custom-icon" src="@/assets/havr-icon/keychain.svg"> <p class="custom-label">Keychain</p></sidebar-link>
         <sidebar-link v-on:click.native="emptyMapStepper"  v-if="getCurrentRoute === 'Users'" to="/users"><img class="custom-icon"  src="@/assets/havr-icon/users-active.svg"> <p class="custom-label active-link">Users</p></sidebar-link>
          <sidebar-link v-on:click.native="emptyMapStepper"  v-if="getCurrentRoute !== 'Users'" to="/users"><img class="custom-icon"  src="@/assets/havr-icon/users.svg"> <p class="custom-label">Users</p></sidebar-link>
      </template>
      <mobile-menu>
        <li class="divider"></li>
      </mobile-menu>
    </side-bar>
    <div class="main-panel" :class="{'overview-content': getCurrentRoute === 'Overview'}">
      <top-navbar v-if="getCurrentRoute !== 'Keychain-map-list'"></top-navbar>
       <TopNavBarMapList v-if="getCurrentRoute === 'Keychain-map-list'" />
       <TopNavbarDetailsGroup v-if="getCurrentRoute === 'Keychain-map-details'" />
        <TopNavbarEditFloorMaps v-if="getCurrentRoute === 'Keychain-map-edit'" />
      <dashboard-content @click.native="toggleSidebar">
      </dashboard-content>
      <content-footer v-if="getCurrentRoute === 'Keychain'"></content-footer>
       <user-footer v-if="getCurrentRoute === 'Users'"></user-footer>
    </div> 
  </div>
</template>
<style lang="scss">
</style>
<script>
import store from "@/flulux/store";
import TopNavbar from "./TopNavbar.vue";
import ContentFooter from "./ContentFooter.vue";
import UserFooter from "./UserTableFooter";
import DashboardContent from "./Content.vue";
import TopNavbarEditFloorMaps from './TopNavbarEditFloorMap.vue'
import MobileMenu from "./MobileMenu";
import TopNavbarDetailsGroup from './TopNavbarDetailsGroup'
import TopNavBarMapList from "./TopNavBarMaps";
import { dataRepository } from "@/repository/login";
export default {
  data() {
    return {
      show: true,
      store: store
    };
  },
  components: {
    TopNavbar,
    ContentFooter,
    DashboardContent,
    MobileMenu,
    UserFooter,
    TopNavbarDetailsGroup,
    TopNavBarMapList,
    TopNavbarEditFloorMaps
  },
  computed: {
    getCurrentRoute() {
      return this.$route.name;
    }
  },
  methods: {
    toggleSidebar() {
      if (this.$sidebar.showSidebar) {
        this.$sidebar.displaySidebar(false);
      }
    },
    emptyMapStepper() {
      store.state.map.floor_map.picture = null;
      store.state.map.floor_map.name = null;
      store.state.map.floor_map.tutorial = false;
      store.state.map.drawer = false;
    }
  }
};
</script>

<style>
.overview-content {
  background: #fafafa !important;
}
.custom-icon {
  font-size: 22px;
  float: left;
  margin-right: 15px;
  line-height: 30px;
  width: 22px;
  position: relative;
  top: 2px;
  text-align: center;
}

.custom-icon path {
  fill: #000;
}

.active-link {
  color: black !important;
}
.custom-label {
  margin: 0;
  line-height: 30px;
  font-size: 17px;
  color: #a6a6a6;
}
</style>
