import { userDataRepository } from "@/repository/users";
import usersRepository from "@/repository/users";
import moment from "moment";

export default {
  parseDataTable(p) {
    let data = [];
    const userIds = p.map(i => i.user.id);
    usersRepository.retrieveContactPictures(userIds).then(res => {
      res.forEach(user => {
        p.forEach(i => {
          if (user.user_id === i.user.id) {
            i.profile_picture = user.base64;
          }
        });
      });
      p.map(async i => {
        await data.push({
          id: i.user.id,
          profile_picture: i.profile_picture,
          name: i.user.first_name + " " + i.user.last_name,
          username: i.user.username,
          "last activity": this.getLastAcivity(i.last_activity)
        });
      });
      userDataRepository.usersTable = [...data];
    });
  },
  getLastAcivity(p) {
    if (!p) return "No recent activity";
    return this.getDiffTime(p.updated_at) + " ago";
  },

  getDiffTime(p) {
    return moment
      .duration(
        moment
          .utc(p)
          .local()
          .diff(moment.utc().local())
      )
      .humanize();
  }
};
