<template>
<div class="list-group" style="width: 100%">
    <div v-on:dragstart="drag($event, key.id)" draggable="true" @click='addToContainer(key)'  v-for="key in group.keys" :key="key.id" v-if="group.locks.includes(key.id)" :class="{'disabled-marker': store.state.map.usedKeys.includes(key.id) }" class="list-group-item list-group-item-action key-list-selector"> <img class="dot-selector" v-show="store.state.map.usedKeys.includes(key.id) " src="@/assets/havr-icon/dot-key-selector-disabled.svg"> 
    <img class="dot-selector" v-show="!store.state.map.usedKeys.includes(key.id)" src="@/assets/havr-icon/dot-key-selector.svg"> <span class="key-name">{{key.description}}</span></div>
</div>
</template>

<script>
import store from "@/flulux/store";
export default {
  props: ["group"],
  data() {
    return {
      store: store
    };
  },
  mounted() {
    if (!this.$root._events.ADD_NEW_KEY) {
      this.$root.$on("ADD_NEW_KEY", (e, param) =>
        this.addToContainer(e, param)
      );
    }
  },
  methods: {
    drag(e, id) {
      e.dataTransfer.setData("application/node type", this);
      this.store.state.map.draggedKeyFromList = id;
      this.store.state.map.floor_map.tutorial = false;
    },
    addToContainer(e, param) {
      const relativeX = param.x - param.offset.left;
      const relativeY = param.y - param.offset.top;
      this.store.state.map.currentFloor.markers.push({
        x: relativeX / (param.clientWidth * param.scale) * 100,
        y: relativeY / (1000 * param.scale) * 100,
        door_lock_id: this.store.state.map.draggedKeyFromList
      });
      this.store.state.map.usedKeys.push(
        this.store.state.map.draggedKeyFromList
      );
      this.$notify({
        message: "Key added on map",
        type: "success",
        horizontalAlign: "right",
        timeout: 2000,
        verticalAlign: "top"
      });
      this.store.state.map.draggedKeyFromList = null;
    }
  }
};
</script>

<style scoped>
.key-list-selector {
  border: none;
  background: #fdfdfd;
}
.dot-selector {
  margin-bottom: 3px;
  margin-left: 5px;
}
.key-name {
  margin-left: 15px;
}

.disabled-marker {
  pointer-events: none;
  color: #dcdcdc;
}
</style>

