import { ENDPOINTS, API_BASE_URL } from "../constant.js";
import axios from "axios";
export default {
  /**
   * Returns all the floor map groups of the current user.
   */
  async getAllFloorGroupMaps() {
    const response = await fetch(API_BASE_URL + ENDPOINTS.floor_map_groups, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      }
    });
    const data = await response.json();
    return data;
  },
  async getOneFloorGroupMaps(id) {
    const response = await fetch(
      API_BASE_URL + ENDPOINTS.floor_map_groups + "/" + id,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer: " + sessionStorage.getItem("token")
        }
      }
    );
    const data = await response.json();
    return data;
  },
  async getAllFloorMaps() {
    const response = await fetch(API_BASE_URL + ENDPOINTS.maps, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      }
    });
    const data = await response.json();
    return data;
  },
  async getAllFloorGroupMapsWithFloorMapsAndPictures() {
    const response = await fetch(
      API_BASE_URL + ENDPOINTS.groups_with_floor_maps,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer: " + sessionStorage.getItem("token")
        }
      }
    );
    const data = await response.json();
    return data;
  },
  async getOneFloorMaps(id) {
    const response = await fetch(
      API_BASE_URL + ENDPOINTS.floor_map_details + id,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer: " + sessionStorage.getItem("token")
        }
      }
    );
    const data = await response.json();
    return data;
  },
  async addNewFloorMap(obj) {
    let group = await this.getAllFloorGroupMaps();
    if (group.length === 0) {
      group.push({ id: null });
    }
    let floorMap = {
      floor_map: {
        name: obj.name,
        floor_map_group_id: group[0].id,
        positions: JSON.stringify(obj.markers),
        file_extension: "png",
        base64_picture: obj.picture
      }
    };
    if (!floorMap.floor_map.floor_map_group_id) {
      delete floorMap.floor_map.floor_map_group_id;
    }
    const response = await fetch(API_BASE_URL + ENDPOINTS.floor_map, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      },
      body: JSON.stringify(floorMap)
    });
    const data = await response.json();
    return data;
  },
  async updateFloorMap(p) {
    const id = p.id;
    p.positions = JSON.stringify(p.markers);
    delete p.markers;
    delete p.id;
    delete p.showHover;
    delete p.existing_status;
    p.base64_picture = p.picture;
    delete p.picture;
    const res = await axios({
      method: "patch",
      url: API_BASE_URL + ENDPOINTS.floor_map + id,
      data: {
        floor_map: p
      },
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer: " + sessionStorage.getItem("token")
      }
    });

    if (res.status !== 200) {
      return false;
    } else {
      return true;
    }
  }
};

export let mapDataRepository = {
  floor_maps: []
};
