<template>
  <div>
    <right-details-drawer/>
    <div
      id="content"
      class="content"
      v-bind:style="{ backgroundImage: 'url(' + 'data:image/png;base64,'+ store.state.map.currentFloor.picture + ')', transform: 'scale(' + this.scale +')' }"
    >
      <img
        draggable="false"
        v-if="showMarkers"
        v-for="marker in store.state.map.currentFloor.markers"
        :key="marker"
        :style="{left: marker.x + '%', top: marker.y + '%', 'margin-left': '-2em', 'margin-top': '-2em'}"
        @click="setDetailsItem(marker.door_lock_id)"
        src="@/assets/havr-icon/key-marker.svg"
        class="key-marker"
      >
    </div>

    <nav class="zoom">
      <ul class="pagination">
        <li @click="zoom(true)" class="page-item disabled zoom-paginator">
          <a class="page-link text-secondary" href="#" tabindex="-1">+</a>
        </li>
        <li @click="zoom(false)" class="page-item disabled zoom-paginator">
          <a class="page-link text-secondary" href="#" tabindex="-1">-</a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import store from "@/flulux/store";
import lodash from "lodash";
import notifiy from "vue-notification";
import RightDetailsDrawer from "@/layout/dashboard/RightDetailsDrawer";
import { keychainRepository } from "@/repository/keychain";
import { userDataRepository } from "@/repository/users";
import userRepository from "@/repository/users";
import repository from "@/repository/keychain";
export default {
  data() {
    return {
      store: store,
      userDataRepository: userDataRepository,
      notificationToReturn: [],
      accessManagement: {
        admin: [],
        users: []
      },
      selectedFloor: 1,
      showMarkers: true,
      scale: 0.7
    };
  },
  components: {
    RightDetailsDrawer
  },
  methods: {
    zoom(param) {
      if (param) {
        this.scale = this.scale + 0.2;
      } else {
        this.scale = this.scale - 0.2;
      }
    },
    setSelectedFloor(p) {
      this.showMarkers = false;
      this.selectedFloor = p;
      this.showMarkers = !this.showMarkers;
    },
    async setDetailsItem(id) {
      const p = await repository.getSpecificLockDoor(id);
      if (p.access === "Normal") {
        return this.$notify({
          message:
            "Details lock door is not available with " + p.access + " access",
          type: "danger",
          horizontalAlign: "right",
          timeout: 2000,
          verticalAlign: "top"
        });
      }

      if (p.status === "inactive") {
        return this.$notify({
          message:
            "Details lock door is not available with " + p.status + " status",
          type: "danger",
          timeout: 2000,
          horizontalAlign: "right",
          verticalAlign: "top"
        });
      }
      this.$notify({
        message: "Selected key : " + p.description,
        type: "info",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
      this.notificationToReturn = [];
      const setNullNotification = await this.store.actions.SET_ACTIVITY([]);
      const setNullAccessManagement = await this.store.actions.SET_ACCESS_MANAGEMENT(
        []
      );
      this.accessManagement = {
        admin: [],
        users: []
      };
      this.store.state.keychain.keylog = [];
      let obj = p;
      obj.lock_group_description = await this.getLockGroupFromId(
        obj.lock_group_id
      );

      const setDetailsKey = await this.store.actions.SET_DETAILS_KEYCHAIN(obj);
      const accessManagement = await repository.getAccessManagement(obj.id);

      const parseAccessManagement = await this.getAccessManagement(
        accessManagement
      );
      const setAccessManagement = await this.store.actions.SET_ACCESS_MANAGEMENT(
        this.accessManagement
      );
      const logs = await repository.getLogs(obj.id);
      this.store.actions.OPEN_DETAILS_DRAWER(true);
      if (keychainRepository.actvityKey.length === 0)
        return this.store.actions.SET_ACTIVITY(false);
      const parseLogs = await this.getLogs();
      this.store.actions.SET_ACTIVITY(this.notificationToReturn);
    },
    async getLockGroupFromId(p) {
      const res = await repository.getLockGroupFromId(p);
      if (res.parent_lock_group) {
        return res.parent_lock_group.description;
      }
      return res.description;
    },
    async getAccessManagement(p) {
      const res = await Promise.all(
        p.map(async i => {
          const resp = await this.buildLogs(i);
          if (resp && resp.admin) {
            this.accessManagement.admin.push(resp);
          }
          if (resp && !resp.admin) {
            this.accessManagement.users.push(resp);
          }
        })
      );
      return res;
    },
    async buildLogs(i) {
      if (i.user_id) {
        i.fullName = undefined;
        let res = await userRepository.retrieveUser(i.user_id);
        if (res === false) {
          return;
        }
        const picture = lodash.filter(userDataRepository.contactPictures, {
          id: i.user_id
        });
        if (picture.length !== 0) {
          i.picture = picture[0].img;
        } else {
          const res = await userRepository.retrieveContactPicture(i.user_id);
          if (res !== false) {
            i.picture = res.base64;
          } else {
            i.picture = undefined;
          }
        }
        i.fullName = res.first_name + " " + res.last_name;
        return i;
      }
    }
  }
};
</script>

<style scoped>
.key-marker {
  position: absolute;
  cursor: pointer;
}
.content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  background-repeat: no-repeat;
  width: 100%;
  background-size: 100% 100%;
  height: 1000px;
  position: absolute;
  overflow: auto;
  margin: 0 auto;
}

.selected-floor {
  color: black !important;
  font-weight: 600;
}

.floor-paginator {
  width: 40px;
  text-align: -webkit-center;
}

.floor-pagination {
  position: fixed;
  bottom: 0;
  margin-left: 3%;
}

.zoom-paginator {
  width: 40px;
  text-align: -webkit-center;
}

.zoom {
  position: fixed;
  bottom: 0;
  right: 0;
  margin-right: 3%;
}
</style>