<template>
  <div>
    <table class="table margin" :class="tableClass">
      <thead>
        <slot name="columns">
          <th
            class="capitalize paragraph-small-regular userWidthTh"
            v-for="column in columns"
            :key="column"
          >
            {{column}}
            <img class="filter-th" src="@/assets/havr-icon/filterTableIcon.svg">
          </th>
        </slot>
      </thead>
      <tbody>
        <tr @click="setUserDetails(item)" v-for="(item, index) in data" :key="index">
          <slot :row="item">
            <td
              style="cursor: pointer;"
              class="margin-td capitalize"
              v-for="(column, index) in columns"
              :key="index"
            >
              <div class="container-table">
                <div class="valid" v-if="column === 'status' && item[column] === 'active'">
                  <i class="ti-check light"></i>
                </div>
                <div v-if="column === 'name'">
                  <div
                    v-if="item.profile_picture"
                    class="table-profile-picture"
                    :style="{'background-image': 'url(data:image/png;base64,'+ item.profile_picture + ')'}"
                  ></div>
                  <img v-else class="table-profile-picture" src="@/assets/img/user.png">
                </div>
                <div v-if="column === 'status' && item[column] === 'inactive'">
                  <i class="ti-close inactive"></i>
                </div>
                <div class="table-data">
                  <span>{{item[column]}}</span>
                </div>
              </div>
            </td>
          </slot>
        </tr>
        <div class="fixed-footer-separator"></div>
      </tbody>
    </table>
  </div>
</template>
<script>
import store from "@/flulux/store";
import lodash from "lodash";
import notifiy from "vue-notification";
import userRepository from "@/repository/users";
import { userDataRepository } from "@/repository/users";
export default {
  data() {
    return {
      store: store,
      notificationToReturn: [],
      accessManagement: {
        admin: [],
        users: []
      }
    };
  },
  props: {
    columns: Array,
    data: Array,
    type: {
      type: String, // striped | hover
      default: "striped"
    },
    maxRow: {
      type: Number,
      default: 0
    },
    title: {
      type: String,
      default: ""
    },
    subTitle: {
      type: String,
      default: ""
    }
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  mounted() {
    this.store.actions.OPEN_USER_DETAIL_DRAWER(false);
  },
  methods: {
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    async setUserDetails(p) {
      const obj = lodash.filter(userDataRepository.contacts, {
        user: { id: p.id }
      });
      obj[0].shared_keys = await userRepository.retrieveSharedKeyForUser(p.id);
      obj[0].logs = await userRepository.retrieveLockDoorUserDetail(p.id);
      this.store.state.user.indexShowAll = 3;
      const setDetailsUser = await this.store.actions.SET_DETAILS_USER(obj[0]);
      const drawer = await this.store.actions.OPEN_USER_DETAIL_DRAWER(true);
    }
  }
};
</script>

<style>
.table-profile-picture {
  width: 32px;
  border-radius: 6px;
  height: 32px;
  background-size: cover;
  background-position: 50% 50%;
  margin-right: 24px;
}

.fixed-footer-separator {
  height: 15px;
}

@-moz-document url-prefix() {
  .fixed-footer-separator {
    height: 65px;
  }
}

.userWidthTh {
  width: 33%;
}

.table-data {
  display: flex;
  flex: 1;
  align-items: center;
}
</style>
