<template>
  <div id="wrapper" :class="{'toggled': store.state.user.drawer === false}">
    <div id="sidebar-wrapper">
      <ul class="sidebar-nav">
        <li class="sidebar-brand">
          <transition name="fade">
            <img
              v-if="store.state.user.drawer === true"
              @click="store.actions.OPEN_USER_DETAIL_DRAWER(false), store.state.user.indexShowAll = 3"
              class="close-icon close-img"
              src="@/assets/havr-icon/close-drawer-icon.svg"
            >
          </transition>
        </li>
        <center>
          <li>
            <transition name="fade">
              <div
                v-if="store.state.user.detailsUser.profile_picture"
                class="avatar-profile"
                :style="{'background-image': 'url('+ 'data:image/png;base64,'+  store.state.user.detailsUser.profile_picture +')', 'background-size': 'cover', 'background-position': '50% 50%'}"
              ></div>
              <img v-else class="avatar-profile" src="@/assets/img/user.png">
            </transition>
          </li>

          <li>
            <span
              class="user-name"
            >{{store.state.user.detailsUser.user.first_name}} {{store.state.user.detailsUser.user.last_name}}</span>
            <br>
            <span class="user-tag" href="#">{{store.state.user.detailsUser.user.username}}</span>
          </li>
          <br>
        </center>

        <ul class="item-icon">
          <li class="sub-item flag">
            <span class="item-sub-text"></span>
          </li>
        </ul>
        <hr>
        <li class="item">
          <p
            class="sub-nav-user"
            href="#"
          >Has access to {{store.state.user.detailsUser.shared_keys.length}} keys</p>
          <span
            v-if="store.state.user.indexShowAll === 3 && store.state.user.detailsUser.shared_keys.length > 4"
            @click="expandShowAll()"
            class="showAll"
          >show all</span>
          <span
            v-if="store.state.user.indexShowAll >= 4"
            @click="expandShowAll()"
            class="showAll"
          >close</span>
        </li>

        <li
          v-for="item in store.state.user.detailsUser.shared_keys.slice(0, (store.state.user.indexShowAll + 1))"
        >
          <span style="display: flex;margin-bottom: 5px;">
            <div class="valid" v-if=" item.door_lock.existing_status === 'active'">
              <i class="ti-check light"></i>
            </div>
            <div v-if="item.door_lock.existing_status === 'inactive'">
              <i class="ti-close inactive"></i>
            </div>
            {{item.door_lock.description}}
          </span>
        </li>

        <hr>

        <li class="item">
          <p
            class="sub-nav-user"
            href="#"
          >{{store.state.user.detailsUser.user.first_name}}'s activity</p>
        </li>
        <div v-if="store.state.user.detailsUser.logs.length > 0">
          <li v-for="i in orderLog" v-if="getLockDoorFromId(i.door_lock_id) !== false" class="item">
            <div class="text-item lock-open-notification">
              <div class="door-state" v-if="i.door_lock_status === 'locked'">closed</div>
              <div v-else class="door-state">opened</div>
              <div v-if="i.door_lock_status === 'locked'" class="dot-closed lock-open-notification"></div>
              <div v-else class="dot-opened lock-open-notification"></div>
              {{getLockDoorFromId(i.door_lock_id)}}
              <div class="diff-time">{{getDiffTime(i.created_at)}} ago</div>
            </div>
          </li>
        </div>

        <li v-if="store.state.user.detailsUser.logs.length === 0">
          <p class="text-item">No recent activity</p>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import store from "@/flulux/store";
import moment from "moment";
import lodash from "lodash";
import { userDataRepository } from "@/repository/keychain";
export default {
  data() {
    return {
      store: store,
      userDataRepository: userDataRepository
    };
  },
  computed: {
    orderLog: function() {
      return this.store.state.user.detailsUser.logs.sort(
        (a, b) => new Date(b.created_at) - new Date(a.created_at)
      );
    }
  },
  methods: {
    expandShowAll() {
      if (
        this.store.state.user.indexShowAll ===
        this.store.state.user.detailsUser.shared_keys.length
      ) {
        return (this.store.state.user.indexShowAll = 3);
      }
      return (this.store.state.user.indexShowAll = this.store.state.user.detailsUser.shared_keys.length);
    },

    getLockDoorFromId(p) {
      const keyName = lodash.filter(
        this.store.state.user.detailsUser.shared_keys,
        {
          door_lock_id: p
        }
      );
      if (keyName.length === 0) return false;
      return keyName[0].door_lock.description;
    },
    /**
     * get time notification
     */
    getDiffTime(p) {
      const now = moment.utc(p).local();
      const end = moment.utc().local();
      return moment.duration(now.diff(end)).humanize();
    }
  }
};
</script>
