<template>
  <div>
    <router-link to="/login">
      <img class="close-account-page" src="@/assets/havr-icon/close-drawer-icon.svg">
    </router-link>
    <div class="form-stepper-create-account">
      <div class="jumbo">
        <h2>
          <b class="heading-x-large">Create your Havr account</b>
        </h2>
        <p
          class="paragraph-medium-regular-secondary"
        >Please enter or click a button to validate a line</p>
      </div>
      <br>
      <br>

      <div :class="{'hide-form' : currentStep < stepper.one}" class="section-form">
        <label
          class="paragraph-medium-regular"
          for="exampleInputEmail1"
        >1. Hello there, what's your firstname ?</label>
        <div class="form-inline">
          <div class="form-group">
            <input
              @click="currentStep = 1"
              @keyup.enter="trigger('step2', 'normal', user.first_name)"
              v-model="user.first_name"
              type="text"
              class="form-control form-control-lg custom-size havr-input"
              aria-describedby="emailHelp"
              placeholder="eg. Jhon"
              required
            >
            <div class="invalid-feedback">Please enter your first name.</div>
          </div>
          <button
            id="formButton"
            v-if="currentStep === 1 && user.first_name.length > 0"
            @click="trigger('step2', 'normal', user.first_name)"
            class="next-button-create-account margin-left"
          >Next</button>
        </div>
      </div>

      <div :class="{'hide-form' : currentStep < stepper.two}" class="section-form">
        <label class="paragraph-medium-regular" for="exampleInputEmail1">2. And your last name ?</label>
        <div class="form-inline">
          <div class="form-group">
            <input
              @click="currentStep = 2"
              @keyup.enter="trigger('step3', 'normal', user.last_name)"
              v-model="user.last_name"
              :disabled="user.first_name.length === 0"
              type="text"
              class="form-control form-control-lg custom-size havr-input"
              aria-describedby="emailHelp"
              placeholder="eg. Doe"
              ref="step2"
              required
            >
            <div class="invalid-feedback">Please enter your last name.</div>
          </div>
          <button
            id="formButton"
            v-if="currentStep === stepper.two && user.last_name.length > 0"
            @click="trigger('step3', 'normal', user.last_name)"
            class="next-button-create-account margin-left"
          >Next</button>
        </div>
      </div>
      <div :class="{'hide-form' : currentStep < stepper.three}" class="section-form">
        <label class="paragraph-medium-regular" for="exampleInputEmail1">3. What's your email ?</label>
        <div class="form-inline">
          <div class="form-group">
            <input
              @click="currentStep = 3"
              @keyup.enter="trigger('step4', 'email', user.email)"
              v-model="user.email"
              :disabled="user.last_name.length === 0 && currentStep < stepper.three"
              type="email"
              class="form-control form-control-lg custom-size havr-input"
              aria-describedby="emailHelp"
              placeholder="eg. jhon@doe.com"
              ref="step3"
              required
            >
            <div class="invalid-feedback">Please enter your email.</div>
          </div>
          <button
            id="formButton"
            v-if="currentStep === stepper.three && checkEmailProcess === false"
            @click="trigger('step4', 'email', user.email)"
            class="next-button-create-account margin-left"
          >Next</button>
          <div v-if="checkEmailProcess" class="sk-circle">
            <div class="sk-circle1 sk-child"></div>
            <div class="sk-circle2 sk-child"></div>
            <div class="sk-circle3 sk-child"></div>
            <div class="sk-circle4 sk-child"></div>
            <div class="sk-circle5 sk-child"></div>
            <div class="sk-circle6 sk-child"></div>
            <div class="sk-circle7 sk-child"></div>
            <div class="sk-circle8 sk-child"></div>
            <div class="sk-circle9 sk-child"></div>
            <div class="sk-circle10 sk-child"></div>
            <div class="sk-circle11 sk-child"></div>
            <div class="sk-circle12 sk-child"></div>
          </div>
        </div>
      </div>
      <div :class="{'hide-form' : currentStep < stepper.four}" class="section-form">
        <label class="paragraph-medium-regular" for="exampleInputEmail1">4. Time to choose password</label>
        <div class="form-inline">
          <div class="form-group">
            <input
              @click="currentStep = 4"
              @keyup.enter="trigger('step5', 'password', user.password)"
              v-model="user.password"
              :disabled="user.email.length === 0"
              type="password"
              class="form-control form-control-lg custom-size havr-input"
              aria-describedby="emailHelp"
              placeholder="8 characters min"
              ref="step4"
              required
            >
            <div class="invalid-feedback">Please enter a password.</div>
          </div>
          <button
            id="formButton"
            v-if=" currentStep === stepper.four && user.password"
            @click="trigger('step5', 'password', user.password)"
            class="next-button-create-account margin-left"
          >Next</button>
        </div>
      </div>
      <div :class="{'hide-form' : currentStep < stepper.five}" class="section-form">
        <label class="paragraph-medium-regular" for="exampleInputEmail1">5. Please confirm password</label>
        <div class="form-inline">
          <div class="form-group">
            <input
              @click="currentStep = 5"
              @keyup.enter="trigger('step6', 'samePassword', checkPasswordData)"
              v-model="checkPasswordData"
              :disabled="user.password.length === 0"
              type="password"
              class="form-control form-control-lg custom-size havr-input"
              aria-describedby="emailHelp"
              ref="step5"
              placeholder="same as before"
              required
            >
            <div class="invalid-feedback">Please confirm your password.</div>
          </div>
          <button
            id="formButton"
            v-if="checkPasswordData.length > 0 && currentStep === stepper.five "
            @click="trigger('step6', 'samePassword', checkPasswordData)"
            class="next-button-create-account margin-left"
          >Next</button>
        </div>
      </div>
      <div :class="{'hide-form' : currentStep < stepper.six}" class="section-form">
        <label
          class="paragraph-medium-regular"
          for="exampleInputEmail1"
        >6. Finally, choose a username</label>

        <div class="form-group">
          <input
            @keyup.enter="checkUsernameAndGoNextStep()"
            v-model="user.username"
            type="text"
            class="form-control form-control-lg custom-size havr-input"
            aria-describedby="emailHelp"
            ref="step6"
            placeholder="eg. jhon02"
            required
          >
          <div class="invalid-feedback">Please enter a username.</div>
        </div>
        <br>
        <button
          v-show="!canCreateAccount"
          v-if="currentStep === stepper.six && !checkUsernameProcess"
          @click="checkUsernameAndGoNextStep()"
          class="next-button-create-account margin-left"
        >Create account</button>
        <div v-if="checkUsernameProcess" class="sk-circle">
          <div class="sk-circle1 sk-child"></div>
          <div class="sk-circle2 sk-child"></div>
          <div class="sk-circle3 sk-child"></div>
          <div class="sk-circle4 sk-child"></div>
          <div class="sk-circle5 sk-child"></div>
          <div class="sk-circle6 sk-child"></div>
          <div class="sk-circle7 sk-child"></div>
          <div class="sk-circle8 sk-child"></div>
          <div class="sk-circle9 sk-child"></div>
          <div class="sk-circle10 sk-child"></div>
          <div class="sk-circle11 sk-child"></div>
          <div class="sk-circle12 sk-child"></div>
        </div>
      </div>

      <img class="side-brand" src="@/assets/img/havr-side-brand.svg">
    </div>
  </div>
</template>



<script>
import repository from "@/repository/users";
import notifiy from "vue-notification";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      checkPasswordData: "",
      currentStep: 1,
      checkEmailProcess: false,
      emailIsAlreadyTaken: false,
      usernameIsAlreadyTaken: false,
      checkUsernameProcess: false,
      user: {
        email: "",
        username: "",
        first_name: "",
        last_name: "",
        password: ""
      },
      stepper: {
        one: 1,
        two: 2,
        three: 3,
        four: 4,
        five: 5,
        six: 6
      },
      passwordRegex: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d.*)(?=.*\W.*)[a-zA-Z0-9\S]{8,50}$/m,
      usernameRegex: /[^A-Za-z0-9]/gm,
      emailRegex: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    };
  },
  computed: {
    canCreateAccount() {
      for (const i in this.user) {
        if (this.user[i].trim() === "") return true;
      }
    }
  },
  methods: {
    checkPassword(p) {
      if (this.user.password.length < 16) {
        const regex = RegExp(this.passwordRegex);
        if (!regex.test(this.user.password)) return this.notifyBadPassword();
      }
      this.currentStep++;
      this.$refs[p].focus();
    },
    checkSamePassword(p) {
      if (this.user.password !== this.checkPasswordData) {
        this.$notify({
          message: "Error : Password dont match",
          type: "danger",
          timeout: 2000,
          horizontalAlign: "right",
          verticalAlign: "top"
        });
        return false;
      }
      this.currentStep++;
      this.$refs[p].focus();
    },
    async checkUsernameAndGoNextStep() {
      const regex = RegExp(this.usernameRegex);
      if (regex.test(this.user.username)) return this.notifyBadFormatUsername();
      this.checkUsernameProcess = true;
      const res = await repository.checkUsernameOrEmail(this.user.username);
      this.checkUsernameProcess = false;
      if (res.already_exists) {
        this.usernameIsAlreadyTaken = true;
        return this.notifyEmailOrUsernameError("Username");
      } else this.createAccount();
    },
    async checkEmailAndGoNextStep(p) {
      const regex = RegExp(this.emailRegex);
      if (!regex.test(this.user.email))
        return this.notifyBadFormatEmail("Email");
      this.checkEmailProcess = true;
      const res = await repository.checkUsernameOrEmail(this.user.email);
      this.checkEmailProcess = false;
      if (res.already_exists) return (this.emailIsAlreadyTaken = true);
      else {
        this.emailIsAlreadyTaken = false;
        this.currentStep++;
        this.$refs[p].focus();
      }
    },
    trigger(p, type, s) {
      if (s.length === 0) return;
      if (type === "email") {
        return this.checkEmailAndGoNextStep(p).then(res => {
          if (this.emailIsAlreadyTaken)
            return this.notifyEmailOrUsernameError("Email");
        });
      }
      if (type === "password") {
        return this.checkPassword(p);
      }
      if (type === "username") {
        return this.checkUsernameAndGoNextStep().then(res => {
          if (!res) return this.notifyEmailOrUsernameError("Username");
          this.currentStep++;
          this.$refs[p].focus();
        });
      }
      if (type === "samePassword") {
        return this.checkSamePassword(p);
      } else {
        this.currentStep++;
        this.$refs[p].focus();
      }
    },
    notifyEmailOrUsernameError(p) {
      this.$notify({
        message: "Error : " + p + " already taken",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    notifyBadFormatEmail(p) {
      this.$notify({
        message: "Error : email format is incorrect",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    notifyBadFormatUsername() {
      this.$notify({
        message: "Error : username format is incorrect",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    notifyBadPassword() {
      this.$notify({
        message: "Error : password format is incorrect",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    async createAccount() {
      if (this.checkPasswordData !== this.user.password) return;
      const user = { user: this.user };
      const resp = await repository.createUserAccount(user);
      this.successNotification();
    },
    successNotification() {
      return Swal({
        allowOutsideClick: false,
        title: "Success",
        text: "Your account has been created with success !",
        type: "success",
        showCancelButton: false,
        confirmButtonText: "Go to login page"
      }).then(result => {
        if (result.value) {
          this.$router.push("login");
        }
      });
    }
  }
};
</script>


