<template>
  <div class="table-responsive">
    <table class="table margin" :class="tableClass">
      <thead>
        <slot name="columns">
          <th
            class="capitalize h paragraph-small-regular keychainTableWidth"
            v-for="column in columns"
            :key="column"
          >
            {{column}}
            <img
              :id="'filter'+column"
              @click="filterByAscOrDesc(alphabeticalSort, column)"
              class="filter-th"
              src="@/assets/havr-icon/filterTableIcon.svg"
            >
          </th>
        </slot>
      </thead>

      <tbody>
        <tr
          v-for="(item, index) in items.slice(store.state.keychain.startPaginationItem - 1, store.state.keychain.endPaginationItem)"
          :key="index"
        >
          <slot :row="item">
            <td
              :disabled="item['Status'] === 'Inactive' ||  item['Access'] === 'Normal'"
              @click="setDetailsItem(item)"
              class="margin-td capitalize"
              v-for="column in columns"
              :key="column"
            >
              <div class="container-table">
                <div class="valid" v-if="column === 'status' && item[column] === 'active'">
                  <i class="ti-check light"></i>
                </div>

                <div v-if="column === 'status' && item[column] === 'inactive'">
                  <i class="ti-close inactive"></i>
                </div>
                <span>{{item[column]}}</span>
              </div>
            </td>
          </slot>
        </tr>
        <div class="fixed-footer-separator"></div>
      </tbody>
    </table>
  </div>
</template>
<script>
import store from "@/flulux/store";
import lodash from "lodash";
import tippy from "tippy.js";
import notifiy from "vue-notification";
import { keychainRepository } from "@/repository/keychain";
import { userDataRepository } from "@/repository/users";
import userRepository from "@/repository/users";
import repository from "@/repository/keychain";
export default {
  name: "paper-table",
  data() {
    return {
      store: store,
      userDataRepository: userDataRepository,
      notificationToReturn: [],
      accessManagement: {
        admin: [],
        users: []
      },
      items: this.data,
      detailsKey: {},
      alphabeticalSort: true,
      cantClick: false
    };
  },
  props: {
    columns: Array,
    data: Array,
    type: {
      type: String, // striped | hover
      default: "striped"
    },
    maxRow: {
      type: Number,
      default: 0
    },
    title: {
      type: String,
      default: ""
    },
    subTitle: {
      type: String,
      default: ""
    }
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  mounted() {
    this.store.actions.OPEN_DETAILS_DRAWER(false);
  },
  methods: {
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    async setDetailsItem(p) {
      if (this.cantClick === true) {
        return;
      }
      this.store.state.keychain.loadDetails = true;
      if (p.access === "Normal") {
        await this.allowClick(false);
        await this.store.actions.OPEN_DETAILS_DRAWER(false);
        return this.$notify({
          message:
            "Details lock door is not available with " + p.access + " access",
          type: "danger",
          horizontalAlign: "right",
          timeout: 2000,
          verticalAlign: "top"
        });
      }
      if (p.status === "inactive") {
        await this.store.actions.OPEN_DETAILS_DRAWER(false);
        await this.allowClick(false);
        return this.$notify({
          message:
            "Details lock door is not available with " + p.status + " status",
          type: "danger",
          timeout: 2000,
          horizontalAlign: "right",
          verticalAlign: "top"
        });
      }
      await this.allowClick(true);
      await this.emptyDetailsObjet();
      this.detailsKey = {};

      if (p.shared) {
        this.detailsKey = lodash.filter(keychainRepository.shared_key, {
          door_lock_id: p.id
        });
        this.detailsKey[0].id = this.detailsKey[0].door_lock_id;
        this.detailsKey[0].lock_group_id = this.detailsKey[0].door_lock.lock_group_id;
      } else {
        this.detailsKey = lodash.filter(keychainRepository.door_locks, {
          id: p.id
        });
      }
      this.detailsKey[0].lock_group_description = await this.getLockGroupFromId(
        this.detailsKey[0].lock_group_id
      );

      await this.store.actions.SET_DETAILS_KEYCHAIN(this.detailsKey[0]);

      const accessManagement = await repository.getAccessManagement(
        this.detailsKey[0].id
      );
      await this.getAccessManagement(accessManagement);
      await this.store.actions.SET_ACCESS_MANAGEMENT(this.accessManagement);
      await repository.getLogs(this.detailsKey[0].id);
      if (this.store.state.keychain.drawer === false) {
        this.store.actions.OPEN_DETAILS_DRAWER(true);
      }

      await this.allowClick(false);
      this.store.state.keychain.loadDetails = false;
      if (keychainRepository.actvityKey.length === 0) {
        return this.store.actions.SET_ACTIVITY(false);
      }

      const parseLogs = await this.getLogs();
      this.store.actions.SET_ACTIVITY(this.notificationToReturn);
    },
    async getLogs() {
      return await Promise.all(
        keychainRepository.actvityKey.map(async i => {
          const resp = await this.buildLogs(i);
          if (resp) {
            this.notificationToReturn.push(resp);
          }
        })
      );
    },
    async allowClick(p) {
      this.cantClick = p;
    },

    filterByAscOrDesc(p, args) {
      if (args === "key name") {
        if (!p) {
          this.items = lodash.orderBy(this.items, ["key name"], ["asc"]);
          this.alphabeticalSort = true;
          this.$notify({
            message: "Keychain list is now ordered by ASC",
            type: "info",
            timeout: 2000,
            horizontalAlign: "right",
            verticalAlign: "top"
          });
        } else {
          this.items = lodash.orderBy(this.items, ["key name"], ["desc"]);
          this.alphabeticalSort = false;
          this.$notify({
            message: "Keychain list is now ordered by DESC",
            type: "info",
            timeout: 2000,
            horizontalAlign: "right",
            verticalAlign: "top"
          });
        }
      }
    },

    async emptyDetailsObjet() {
      this.notificationToReturn = [];
      await this.store.actions.SET_ACTIVITY([]);
      await this.store.actions.SET_ACCESS_MANAGEMENT([]);
      this.accessManagement.admin = [];
      this.accessManagement.users = [];
      this.store.state.keychain.detailsKey = null;
      this.store.state.keychain.keylog = [];
    },
    async getAccessManagement(p) {
      const res = await Promise.all(
        p.map(async i => {
          const resp = await this.buildLogs(i);
          if (resp && resp.admin) {
            this.accessManagement.admin.push(resp);
          }
          if (resp && !resp.admin) {
            this.accessManagement.users.push(resp);
          }
        })
      );
      return res;
    },
    async getLockGroupFromId(p) {
      const res = await repository.getLockGroupFromId(p);
      if (res.parent_lock_group) {
        return res.parent_lock_group.description;
      }
      return res.description;
    },
    async buildLogs(i) {
      if (i.user_id) {
        i.fullName = undefined;
        let res = await userRepository.retrieveUser(i.user_id);
        if (!res) {
          return;
        }
        const picture = lodash.filter(userDataRepository.contactPictures, {
          id: i.user_id
        });
        if (picture.length > 0) {
          i.picture = picture[0].img;
        } else {
          const res = await userRepository.retrieveContactPicture(i.user_id);
          if (res !== false) {
            i.picture = res.base64;
          } else {
            i.picture = undefined;
          }
        }
        i.fullName = res.first_name + " " + res.last_name;
        return i;
      }
    }
  }
};
</script>
<style>
.margin-td {
  padding-bottom: 20px !important;
  padding-top: 20px !important;
  cursor: pointer;
}
.table > tbody > tr {
  position: relative;
  z-index: -1;
}
.capitalize {
  text-transform: capitalize;
}
.table-hover tbody tr:hover {
  background-color: #e6e6e6 !important;
}
.grey {
  color: #e6e6e6;
}
.valid {
  width: 14px;
  height: 14px;
  margin-right: 10px;
  border-radius: 10px;
  background-color: #dcdcdc;
  top: 2px;
  position: relative;
  top: 3px;
}
.ti-check:before {
  content: "\E64C";
  font-size: x-small;
  position: absolute;
  margin-left: 3px;
  margin-top: 3px;
}
.container-table {
  display: flex;
}
.light {
  color: white;
}
.margin {
  position: relative;
  top: 8em;
}
.filter-th {
  margin-left: 25px;
  cursor: pointer;
}
.inactive {
  background: #e74c3c;
  border-radius: 10px;
  color: white;
}
.fixed-footer-separator {
  height: 15px;
}

@-moz-document url-prefix() {
  .fixed-footer-separator {
    height: 65px;
  }
}

.keychainTableWidth {
  width: 25%;
}

thead {
  position: fixed;
  background: white;
  display: table;
  width: calc(100% - 260px);
}
</style>