<template>
  <div>
    <div v-if="maps.floor_maps.length > 0" class="card-group container-position col-12">
      <div class="row">
        <div v-for="map in maps.floor_maps" :key="map.id" class="col-6">
          <div
            @mouseover="map.showHover = true"
            @mouseout="map.showHover = false"
            @click="seeDetails(map)"
            class="card map-card"
          >
            <div class="card-body-map">
              <h5 class="card-title">{{map.name}}</h5>
              <div class="card-text">
                <span>
                  {{map.markers.length}} key
                  <span v-if="map.markers.length > 1">s</span>
                </span>
              </div>
            </div>
            <img
              class="card-img-top"
              :src="'data:image/png;base64,'+ map.picture"
              alt="Card image cap"
            >
          </div>
          <center>
            <div v-if="map.showHover" class="hover-map">
              <ul>
                <li>
                  <center>
                    <img src="@/assets/havr-icon/see-map.svg">
                  </center>
                </li>
                <li>
                  <span class="hover-map-label">See {{map.name}}</span>
                </li>
              </ul>
            </div>
          </center>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import mapRepository from "@/repository/map";
import lodash from "lodash";
import { mapDataRepository } from "@/repository/map";
import store from "@/flulux/store";
export default {
  data() {
    return {
      store: store,
      maps: mapDataRepository
    };
  },
  mounted() {
    if (this.maps.floor_maps.length === 0) {
      this.$router.push("loading-data");
    }
    if (this.maps.floor_maps === null) {
      this.$router.push("Keychain");
    }
  },
  methods: {
    seeDetails(p) {
      this.store.state.map.currentFloor = p;
      this.$router.push("Keychain-map-details");
    }
  }
};
</script>

